import interfaces from '../../utils/interfaces.js'
import goodsHeader from './goodsHeader.vue'
export default {
	data(){
		return{
			goodsData:{
				swiperList:[],
				spec:[],
				comment:[],
				descriptionStr:""
			},
			goodsInfo:{
				name:"",
				price:"",
				number:1,
				spec:""
			},
			currentSwiper:0 //轮播图下表
		}
	},
	components:{
		goodsHeader
	},
	onLoad(option){
		// console.log(option);
		this.goodsInfo = JSON.stringify(option.goodsInfo);
		console.log(this.goodsInfo);
		this.initData();
	},
	methods:{
		initData(){
			this.request({
				url:interfaces.getGoods,
				success:((res)=>{
					this.goodsData = res.data;
				})
			})
		},
		swiperChange(e){
			this.currentSwiper = e.detail.current;
		}
	}
}
